.bracket {
    background-image: linear-gradient($main-brand, $main-brand), linear-gradient($main-brand, $main-brand), linear-gradient($main-brand, $main-brand), linear-gradient($main-brand, $main-brand) !important;
    border-color: $main-brand !important;
}

.tab-button {
    &.active a,
    &.focus a,
    &:active,
    &:focus {
        background: $main-dark-brand !important;
    }
}

.testimonial-item {
    .quote {
        &:before {
            color: $main-brand;
        }

        &:after {
            color: $main-brand;
        }
    }

    .name {
        color: $main-brand;
    }

    .company {
        color: $main-brand;
    }
}

.related-events {
    .related-event {
        background:$main-brand;
    }
}

.icons {
    .bg-main-brand {
        background:$main-brand-alt!important;
    }
}

.term{
    color: $main-brand-alt;
}

.announcement-bar{              
    background-color: $main-brand;                
}

// Filters
.tm-topic-filter,
.tm-region-filter{
    &--control{
        &:before{
            border-color: $main-brand-alt;
        }
    }
}

.nav-filter li.active .checkmark {
    background-color: $main-brand;
}
  
.button-container-alt {  
    color: $main-brand-alt;
}   

// Agenda: Tabs
.custom-agenda-nav {
    li {
        a {
            &:hover {
                background-color: $main-dark-brand-hover;
            }
        }
    }
}


// Agenda: seperator
.ag-pad .row .folded .ag-left{
    &:after{
        background-color: $main-brand-alt;
    }
}